import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'

Vue.use(Vuex);

const store = new Vuex.Store({
    state:{
        // domain: 'http://yqb.shuiwujia.com',

        domain: 'http://user.shuiwujia.cn:5000',
        // domain: 'http://amani.tunnel.qydev.com',
        // domain: 'http://toutiao.tunnel.shuiwujia.cn:8080',
        isShowUser: false,
        isShowNavigation: true,
        newsDetailsData: {},
        isCollectionNew: false,
        count:0,
        collectionNews:[],
        newsArr:[],
        tagNewArr:{},
        logInfo: {},
        code:'',
        isLoading: false,
        isIndexLoading: false,
        urlData: {},
        isSaveUrlData: true,
        jiFen: 0,
        isSearchTextAlert: false,
        searchTextAlert: '',
        isShowQiandaoI: true,
    },
    //使用处进行 commit
    mutations,
    getters:{
        nowTime(state){
            return new Date() - 0 + '-' + state.count;
        },
        getNewsList(state){
            return state.collectionNews;
        },
        getNewsArr(state){
            return state.newsArr;
        },
        getLogInfo(state){
            return state.logInfo;
        }
    }
});
export default store;



