import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'
import socketioTest from '@/components/socketioTest'

Vue.use(Router)

export default new Router({
  routes: [
      // 嵌套路由设置，此处不适用嵌套路由
      // {path: '/index', name:'index', component: index,
      //   children:[
      //       {path: '/index/newsDetails/:id', name: 'newsDetails',component:newsDetails },
      //   ]
      // },
      {
          path: '/socketioTest',
          name:'智能机器人',
          component: socketioTest,
          meta: {
              login: false
          }
      },
      {
          path: '/index',
          name:'智能机器人',
          component: index,
          meta: {
              login: false
          }
      },

      {
          path: '/',
          redirect: '/index'
      },   // 跳转路由，默认显示路由页面
  ],
    'linkActiveClass':'active'  //为路由匹配激活link-active，方便设置类

})



