
import {ajax,getSignlnInfo,saveRemark,getDailypush} from './common/js/ajax'
import { utils } from './common/js/utils'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import Loading from 'vux/src/components/loading/index.vue'

export default {
name: 'app',
components:{
Spinner,Popup,Loading
},
directives: {
TransferDom,
},
data(){
return {
transitionName:'',
isBottom:true,
isShowAlert:true,
isShowInit:true,
bottomArr:['/index','/collection','/follow','/my'],
}
},
methods:{
toggleBody(isPin){

if(isPin){

document.body.style.height = '100vh'

document.body.style['overflow-y'] = 'hidden'
}

else{

document.body.style.height = 'unset'

document.body.style['overflow-y'] = 'auto'

}
}

},
watch: {
'$route' (to, from) {
this.transitionName = JSON.parse(window.sessionStorage.history).transitionName;
let index = this.bottomArr.indexOf(to.path);
if(index == -1){
this.isBottom = false;
}else{
this.isBottom = true;
}
}
},
created(){
var self = this;












},
updated(){

}

}




