/**
 * 封装微信分享方法
 * @param token
 * @param title 页面标题
 * @param shareUrl 分享链接（被分享用户打开的链接）
 * @param shareDescribe 分享描述
 * @param articleGid 页面gID
 * @param sharePicUrl 分享图片封面
 * @param pageId 页面id
 * @param openId
 */
import axios from 'axios'
import store from '../../store'
import { addPointRecord } from  './ajax'
import utils from  './utils'

import qs from 'qs'
function wxShare(wx,wxtitle,title,shareUrl,shareDescribe,articleGid,sharePicUrl,openId){
	// 隐藏所有非基础功能
    wx.hideOptionMenu();

	wx.showMenuItems({
      menuList: [
          'menuItem:share:appMessage', // 分享给朋友
          'menuItem:share:timeline', // 发送到朋友圈
          'menuItem:share:qq', // 发送到QQ
          "menuItem:favorite",
          "menuItem:copyUrl"
      ],
      success: function (res) {
      },
      fail: function (res) {
        // alert(JSON.stringify(res));
      }
    });
	// 分享朋友圈
	wx.onMenuShareTimeline({
      title: wxtitle,
      link: shareUrl,
      imgUrl: sharePicUrl,
      trigger: function (res) {
      },
      success: function (res) {
            setTimeout(()=>{
                //当用户登录后做分享记录
                if(openId){
                    //分享成功后给用户加积分
                    var type = '';
                    if(shareUrl.indexOf('signIn')!=-1){
                        type = 'CIRCLE'
                    }else if(shareUrl.indexOf('dailypushPlaneLayout')!=-1){
                        type = 'DAILY'
                    }else{
                        type = "ARTICLE"
                    }
                        addPointRecord({
                            "openId": openId,
                            "type": type,
                            "typeDetail": window.location.href,
                        },function (data) {
                            store.state.jiFen += data.data.points
                            if(data.data.points==0){
                                store.state.searchTextAlert = "分享成功"
                            }else{
                                store.state.searchTextAlert = "分享成功</br></br>积分+"+data.data.points+"</br>今天已加"+data.data.totalPoints+"积分"
                            }
                            store.state.isSearchTextAlert = true
                            setTimeout(() => {
                                store.state.isSearchTextAlert = false
                            },2000)
                        },function(msg){
                            console.log("用户操作失败")
                        })
                    // }

                    if(type == 'CIRCLE'){
                        // utils.localstorage.savedata("shareState", 0)
                        window.localStorage.setItem("shareState", 0)
                    }
                    axios.post(store.state.domain+'/WaterTopLineBackend/m/saveShareLog.do',
                        qs.stringify({
                            'articleGid': articleGid,
                            'openId': openId,
                            'type': 'wechat_circle'
                        }))
                        .then(function(data){
                            console.log(data? "用户分享朋友圈记录成功": "用户分享朋友圈记录失败");
                            fn1 && fn1(data);
                        })
                        .catch(function(err){
                        });
                }
            },500)
      },
      cancel: function (res) {
      //用户取消分享
          /*
          * 微信开放平台发表声明，取消了用户取消分享的监听
          * 设为统一成功分享回调
          * */
      },
      fail: function (res) {
          alert("分享失败");
      }
    });
	// 分享给朋友
	wx.onMenuShareAppMessage({
        title: title, // 分享标题
        desc: shareDescribe, // 分享描述
        link: shareUrl, // 分享链接
        imgUrl: sharePicUrl, // 分享图标
        type: '', // 分享类型,music、video或link，不填默认为link
        dataUrl: '', // 如果type是music或video，则要提供数据链接，默认为空
        success: function () {
            //当用户登录后做分享记录
            if(openId){
            	axios.post('http://alexfung.tunnel.qydev.com/WaterTopLineBackend/m/saveShareLog.do',
            		qs.stringify({
                		'articleGid': articleGid,
						'openId': openId,
						'type': 'wechat_friend'
                	}))
					.then(function(data){
                		console.log(data? "用户分享好友记录成功": "用户分享好友记录失败");
			            fn1 && fn1(data);
				  	})
				  	.catch(function(err){
				  	});
            }
        },
        cancel: function () {
        },
        fail: function (res) {
        }
    });

    // 分享到QQ
    wx.onMenuShareQQ({
        title: title, // 分享标题
        desc: shareDescribe, // 分享描述
        link: shareUrl, // 分享链接
        imgUrl: sharePicUrl, // 分享图标
        success: function () {
// 用户确认分享后执行的回调函数
            if(openId){
                axios.post('http://alexfung.tunnel.qydev.com/WaterTopLineBackend/m/saveShareLog.do',
                    qs.stringify({
                        'articleGid': articleGid,
                        'openId': openId,
                        'type': 'qq_friend'
                    }))
                    .then(function(data){
                        console.log(data? "用户分享好友记录成功": "用户分享好友记录失败");
                        fn1 && fn1(data);
                    })
                    .catch(function(err){
                    });
            }
        },
        cancel: function () {
// 用户取消分享后执行的回调函数
        }
    });
}export {
  wxShare
}



