import store from '../../store'
import axios from 'axios'
import jsonp from 'jsonp'
import qs from 'qs'


var loading = document.querySelector(".loadEffect");
//加载文章列表
//  **触发条件：切换tag时调用
function ajax(kind,nowpage,fn1,fn2) {
    if(kind){
        store.state.isIndexLoading=true
        //	axios.get('/api/yqbCtr/m/fecthTouTiaoList.do?tagid='+kind+'&pageindex='+nowpage+'&pagesize=10')
        axios.get('http://page.water-it.cn:88/api/TouTiao/data.ashx?type=getdatabypgid&pageindex='+nowpage+'&pagesize=10&pgid='+kind)
            .then(function(data){
                store.state.isIndexLoading=false
                fn1 && fn1(data);
            })
            .catch(function(err){
                store.state.isIndexLoading=false
                console.log(err.message)
                fn2 && fn2(err.message);
            });
    }else{
        return
    }
}
