//加载tag标签
// **触发条件：实例化主页时调用
function getTag(openid,fn1,fn2) {
    //	axios.get('/api/yqbCtr/m/fecthTouTiaoList.do?tagid='+kind+'&pageindex='+nowpage+'&pagesize=10')
    axios.get('http://api.shuiwujia.com/v1/toutiao/categroy?ak=49de119e4367e9722cf71693c3f958b5&openID='+openid)
        .then(function(data){
            fn1 && fn1(data);
        })
        .catch(function(err){
            console.log(err.message)
            fn2 && fn2(err.message);
        });
}

