//微信登录流程_请求跳转
function ajax4(redirectUrl,fn1,fn2) {
	  axios.post(store.state.domain+'/WaterTopLineBackend/m/findOpenId.do',qs.stringify({
	  	'redirectUrl': redirectUrl
	  }))//请求地址
//	  axios.get('http://alexfung.tunnel.qydev.com/WaterTopLineBackend/m/findOpenId.do?redirectUrl='+redirectUrl)//请求地址
	  	.then(function(data){
	  	    //获取回调然后跳外链返回code
//	  	    console.log(data);
            fn1 && fn1(data);
	  	})
	  	.catch(function(err){
            fn2 && fn2(err.message);
	  });

}
//微信登录流程_获取openId
