'http://www.cssf.cc/WaterOracle/File/DownFile/接水申请(居民).doc'
break;
case '参看答案6。':
masgData = '单位用户需要根据业务类别，填写接水申请表，并加盖公章，准备所需资料，到所属营业网点办理。\n' +
'接水申请表也下载地址为：\n' +
'http://www.cssf.cc/WaterOracle/File/DownFile/接水申请(单位).doc\n' +
'具体业务类别和所需资料见表：\n' +
'单位申请接水需带资料\t基建临时接水需带资料:1、9、10项。\t1.1:500平面布置图(楼号为公安编号,住宅小区同时提供其电子文件)。\n' +
'2.管线综合图。\n' +
'3.各楼型的建筑给水底层平面图、给水管系图（包括消防给水）。\n' +
'4.消防主管部门审批（备案）单及审核意见（复印件）\n' +
'5.公安编号批复。\n' +
'6.建筑工程施工许可证。\n' +
'7.建筑工程规划许可证（复印件）。\n' +
'8.用水量情况说明或初步设计文本中给水篇、消防篇\n' +
'9.营业执照。\n' +
'10.法人代表身份证复印件。\n' +
'11.近期水费发票复印件。'
break;
}
this.datas=this.datas.concat(
{
isMyMasg: false,
masgType: 'masg',
headL: require('../../static/img/shenzhen_logo.jpg'),
name: '小深',
data: masgData
}
)
this.masgInput = '';
this.isInputIn  = false;
}
},
created(){

},

}



