

import Vue from 'vue'
import App from './App'


import router from './router/permission.js'

import store from './store/'

import wx from 'weixin-js-sdk'
import VueLazyload from 'vue-lazyload'

import VueResource from 'vue-resource';
Vue.use(VueResource)


import DatetimePlugin from 'vux/src/plugins/datetime/index.js'

Vue.use(DatetimePlugin)



import vueTouch from 'kim-vue-touch'
Vue.use(vueTouch)



Vue.http.headers.common['Access-Control-Allow-Origin']= 'http://user.shuiwujia.cn:5000'
import VueSocketIO from 'vue-socket.io'
import socketio from 'socket.io-client';
Vue.use(new VueSocketIO({
debug: true,
connection: socketio('http://user.shuiwujia.cn:5000/socket',{
extraHeaders: {
'Access-Control-Allow-Origin': '*'
}
}),
}))


Vue.config.productionTip = false;

Vue.use(VueLazyload,{
preLoad: 1.3,
error: './static/img/newspng.png',
loading: './static/img/loading.gif',  //static 目录下的文件要用绝对地址引用 /static/[filename]
attempt: 1,
listenEvents: ['scroll', 'wheel', 'mousewheel', 'resize', 'animationend', 'transitionend', 'touchmove']
});




let history = {
count:4,    //初识tab切换数量
'/index':1,
'/collection':2,
'/follow':3,
'/my':4
};


window.sessionStorage.setItem('history',JSON.stringify(history));


router.beforeEach(function (to, from, next) {
let history = JSON.parse(window.sessionStorage.history);
let historyCount = history.count * 1;
const toIndex = history[to.path];        
const fromIndex = history[from.path];    

if (toIndex) {
if (!fromIndex || parseInt(toIndex) > parseInt(fromIndex) || (toIndex === '0' && fromIndex === '0')) {
history['transitionName'] = 'forward';
} else {
history['transitionName'] = 'reverse';
}
} else {

++historyCount;
history['count'] = historyCount;
to.path !== '/' && (history[to.path] = historyCount);
history['transitionName'] = 'forward';
}

history = JSON.stringify(history);

window.sessionStorage.setItem('history',history);

if (/\/http/.test(to.path)) {
let url = to.path.split('http')[1];
window.location.href = `http${url}`
} else {
next()
}
});



/* eslint-disable no-new */
new Vue({
el: '#app',
router,
wx,
store,    //把 store 对象提供给 “store” 选项，这可以把 store 的实例注入所有的子组件
template: '<App/>',
components: { App }
});






