import store from '../store'
import Vue from 'vue'
import router from './index'
import { utils } from '../common/js/utils'
import {ajax4} from '../common/js/ajax'
import {ajax5} from '../common/js/ajax'
import {ajax6} from '../common/js/ajax'

router.beforeEach((to, from, next) => {
    // //解决微信底部白条影响首页文章实现
    // if(to.fullPath=="/index"){
    //     window.location.hash="#/Jump"
    // }
    //解决ios中地址跟不上（对应不上的问题）
    setTimeout(function () {
        window.location = window.location;
    }, 500);

    //记录用户链接所带参数
    // utils.cookie.setCookie("openId","oKUG71BQS5_5PSkpfPEJrtJUqXH0",2);  			//设置openId过期为一分钟————测试使用——部署删除
    if(store.state.isSaveUrlData){
        //记录用户链接所带参数
        // alert(window.location.href)
        // console.log(to)
        store.state.urlData={
            "scene": to.query.scene?to.query.scene:"",
            "sceneDetail": to.query.sceneDetail?unescape(unescape(to.query.sceneDetail)):"",
            "shareTimes": to.query.shareTimes?to.query.shareTimes:0,
            "remarks": to.query.remarks?to.query.remarks:"",
        }
        store.state.isSaveUrlData = false
    }
    //有openid
    //判断前往的界面是否需要登陆
    if (to.meta.login) {
				//数据仓库和本地数据同步
				store.state.logInfo=utils.localstorage.readdata("userInfo");//
				//退出后进入再次检测是否关注——关注过期就进行查询是否关注
                if(window.sessionStorage.getItem('isSubscribe')!=1){
		        	ajax6(utils.cookie.getCookie("openId"),function (data) {
		        		if(data.data.isSubscribe!=1){
		    				router.push('/follow');
		        		}else{
		        		    var userInfo = data.data;
                            //保存在数据仓库
                            store.state.logInfo=userInfo;
                            //保存在localstorage（utils是写的封装的保存本地数据工具包）
                            utils.localstorage.savedata("userInfo",userInfo);
		        			window.sessionStorage.setItem('isSubscribe',JSON.stringify(data.data.isSubscribe));
			            	next()
		        		}

		        	})
                }else{
                    // ajax6(utils.cookie.getCookie("openId"),function (data) {
                    //     if(data.data.isSubscribe!=1){
                    //         window.sessionStorage.setItem('isSubscribe',JSON.stringify(data.data.isSubscribe));//保存是否关注
                    //     }else{
                    //         next()
                    //     }
                    // })
	            	next()
                }
    }else{

    	//测试-记得删掉oKUG71LIFQrvvqx7ocyQBpVbEhmU
 	    //   utils.cookie.setCookie("openId",'oKUG71BQS5_5PSkpfPEJrtJUqXH0');

        utils.cookie.setCookie("openId","oKUG71BQS5_5PSkpfPEJrtJUqXH0",20000);  			//设置openId过期为一分钟————测试使用——部署删除
        window.sessionStorage.setItem('isSubscribe',1);
 	      utils.localstorage.savedata('userInfo',{"country":"中国","token":"12345678","nickName":"我","sex":1,"openId":"oKUG71BQS5_5PSkpfPEJrtJUqXH0","province":"广东","city":"珠海","headimgurl":"http://thirdwx.qlogo.cn/mmopen/JDgVKLDyzibIQld1yhgQnqnUlZOjGpiclYwEyPe6sAm0jZF521BBEPAcckJp3VUmbP5sLTx2xjy4FVhpNWIW91meE7EzJ8lvhV/132","remark":null,"isSubscribe":1,"subscribeTime":1526026634000,"unsubscribeTime":null})
        // store.state.logInfo=utils.localstorage.readdata("userInfo");
        if(utils.localstorage.readdata("userInfo")){
            store.state.logInfo=utils.localstorage.readdata("userInfo");//////////////注意部署上去的时候取消注释
        }
        //退出后进入再次检测是否关注——关注过期就进行查询是否关注
        if((window.sessionStorage.getItem('isSubscribe'))!=1){
            //sessionStorage没有关注
                ajax6(utils.cookie.getCookie("openId"),function (data) {
                    var userInfo = data.data;
                    //保存在数据仓库
                    store.state.logInfo=userInfo;
                    //保存在localstorage（utils是写的封装的保存本地数据工具包）
                    utils.localstorage.savedata("userInfo",userInfo);
                    window.sessionStorage.setItem('isSubscribe',userInfo.isSubscribe);
                })

        }else{
            // ajax6(utils.cookie.getCookie("openId"),function (data) {
            //     if(data.data.isSubscribe!=1){
            //         window.sessionStorage.setItem('isSubscribe',JSON.stringify(data.data.isSubscribe));//保存是否关注
            //     }else{
            //         next()
            //     }
            // })
        }
        if (to.meta.page) store.state.app.pageLoading = true
        next()
    }

})

router.afterEach((to, from) => {
    if (to.meta.page) store.state.app.pageLoading = false
    document.title = to.name
})

export default router



